@import '../../../sass/variables';

.btn {
  background-color: $color-primary;
  color: #fff;
  border: none;
  border-radius: 0;
  font-family: $font-display;
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background-color: $color-primary-dark;
  }

  &:active {
    background-color: $color-primary;
  }

  &:focus {
    outline: none;
  }
}

.round {
  border-radius: 5rem;
}

.fluid {
  width: 100%;
}
